@import url(https://fonts.googleapis.com/css2?family=Quicksand&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Cormorant+Infant:wght@500;700&display=swap);
body{
    margin-top: 0px;
    margin-right: 0px;
    margin-bottom: 0px;
    margin-left: 0px;
    height: 100%;
    overflow-x: hidden;
    background-color: #fcfcfc;
}

html{
    scroll-behavior: smooth;
    height: 100%;
}

#root{
    height: 100%;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;  
   
}

.heading{
    display: flex;
    height: 110px;
    max-width: 500px;
    align-items: center;
    background-size: 65px;
    background-attachment: fixed;
    z-index: 1;
}












